#information {
    width: 520px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0 50px;
}

#border {
    width: 300px;

    border: 1px solid white;
}

@media screen and (max-width:800px ) {
    #information {
        align-items: center;
    }
}

@media screen and (max-width:600px ) {
    #information {
        width: 340px;
        margin: 0px;
}
}

@media screen and (max-width: 400px) {
    #border {
        width: 200px;
    }
    #information {
        width: 240px;
    }
}
h1 {
    font-weight: 800;
}

@media screen and (max-width:500px ) {
    h1 {
        font-size: 14px;
    }
}


.contact-mode {
    margin: 10px;
}

.contact-mode > a{
    text-decoration: none;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    padding: 12px 22px;
    border: 2px solid #fff;
    margin-right: 11px;
    margin-left: 11px;
}

.contact-mode > a:hover {
    letter-spacing: 1.5px;
    background-color: #8B2252;
}
